/* Variables */
/* Mixins */
/* Global Styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1440px;
  background-color: #FFFFFF;
}

/* Image Styles */
img {
  width: 505px;
  height: 330px;
}

/* Main Content Styles */
h1 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.15rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
}

.desc {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 23.75em;
  height: 6em;
  margin-bottom: 2rem;
}

.btn {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 8rem;
  height: 3rem;
  background-color: #E4C1AE;
  border-radius: 3rem;
  border: none;
  color: #FFFFFF;
}

/* Popup Styles */
/* Layout Styles */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6rem;
  padding-top: 6rem;
}

